.fo-hamburger {
    width: 35px;
    height: 5px;
    background-color: white;
    margin: 6px 0;
  }
  
  .fo-close-menu-button {
    color: white;
    width: 35px;
    height: 39px;
    font-size: 35px;
    font-weight: bold;
  }

  .fo-menu-button {
    background-color: transparent;
    border: transparent;
    width: 38px;
    height: 73px
  }
  
  .fo-menu-button:focus {
    outline:0;
  }
  .fo-hamburger__clicked {
      background-color: grey !important
  }
.fo-menu{
    margin: 7px;
    padding: 0;
    display: none;
}

.fo-menu__open {
    display: inline-block !important;
}

.fo-menu-link {
    display: inline-block;
    border: 1px solid #5eb2d1;
    width: 100%;
    color: #3fa3c9;
    text-align: center;
    font-weight: bold;
    font-size: 15px;
    background-color: white;
    margin: 2px 0; 
}

.fo-menu-link:visited {
    color: #3fa3c9;

}

.fo-menu-link:hover {
    color: #434242;
}
.fo-h1-wrapper {
  margin: 0;
}
.fo-h1 {
  color: white;
  margin: 0;
  margin-top: 5px;
  display: block;
  font-size: 30px;
}

.fo-header {
  height: 76px;
  display: grid;
  grid-template-columns: 48% 52%;
}

.fo-header-container {
  background-color: #1b246f;
  position: fixed;
  top: 0;
  z-index: 1;
}

